import React, { useState } from 'react';
import { useToast } from '@/context/ToastContext';
import { motion } from 'framer-motion';
import { Eye, EyeOff, ArrowLeft } from 'lucide-react';
import { useAuth } from '../context/AuthContext';
import Button from '../ui/Input';
import Input from '../ui/Button';

const AdminLoginPage: React.FC = () => {
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    rememberMe: false,
  });
  const [showPassword, setShowPassword] = useState(false);
  const [errors, setErrors] = useState<Record<string, string>>({});
  const { login, isLoading } = useAuth();
  const { error: showToastError, success: showToastSuccess } = useToast();
  const [submitting, setSubmitting] = useState(false);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value,
    }));
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({ ...prev, [name]: '' }));
    }
  };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};
    
    if (!formData.email) {
      newErrors.email = 'Email is required';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = 'Please enter a valid email';
    }
    
    if (!formData.password) {
      newErrors.password = 'Password is required';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;
    setSubmitting(true);
    try {
      await new Promise(resolve => setTimeout(resolve, 1200));
      await login(formData.email, formData.password, formData.rememberMe);
      showToastSuccess('Login successful', 'Welcome back!');
      window.location.href = '/admin/dashboard';
    } catch (err) {
      let errorMsg = 'Invalid email or password';
      if (err instanceof Error && err.message) {
        errorMsg = err.message;
      } else if (typeof err === 'string') {
        errorMsg = err;
      }
      setErrors(prev => ({ ...prev, general: errorMsg }));
      showToastError('Login failed', errorMsg);
      // Do NOT reset formData here, so values persist
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center p-">
      <div className="w-full max-w-md">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="card rounded-3xl p-8 box-shadow"
        >
          {/* Header */}
          <div className="text-center mb-8">
            <h1 className="text-2xl font-bold text-spiritual-900 mb-2">Sign In</h1>
            <p className="text-spiritual-600">Welcome back!</p>
          </div>

          {/* Form */}
          <form onSubmit={handleSubmit} className="space-y-6">
            {errors.general && (
              <div className="p-3 bg-error-50 border border-error-200 rounded-xl text-error-600 text-sm">
                {errors.general}
              </div>
            )}

            <div>
              <Input
                label="Email ID"
                type="email"
                name="email"
                value={formData.email}
                onChange={handleInputChange}
                placeholder="Enter Email ID"
                error={errors.email}
              />
            </div>
            <div>
              <Input
                label="Password"
                type="password"
                name="password"
                value={formData.password}
                onChange={handleInputChange}
                placeholder="Enter Password"
                showPasswordToggle
                error={errors.password}
              />
            </div>
            <div className="flex items-center">
              <label className="flex items-center space-x-2">
                <input
                  type="checkbox"
                  name="rememberMe"
                  checked={formData.rememberMe}
                  onChange={handleInputChange}
                  className="w-4 h-4 text-primary-600 border-spiritual-300 rounded focus:ring-primary-500"
                />
                <span className="text-sm text-spiritual-600">Remember Me</span>
              </label>
            </div>

            <Button
              type="submit"
              variant="primary"
              size="md"
              className="w-full cursor-pointer"
              loading={isLoading || submitting}
            >
              {submitting ? (
                <span className="flex items-center justify-center gap-2">
                  Signing In...
                </span>
              ) : (
                'Sign In'
              )}
            </Button>

            <div className="text-center mt-4">
              <a href="/admin/register" className="text-primary-600 hover:underline text-sm">
                Don&apos;t have an account? Register
              </a>
            </div>
          </form>
        </motion.div>
      </div>
    </div>
  );
};

export default AdminLoginPage;